<!--热门众测体验报告列表 (大图的)-->
<template>
    <div>
        <ul class="productBox" >
            <li v-for="(item,index) in resData" :key="index" @click="enter(item.id)" >
                <img :src="item.imgSrc" class="pic">
                <div class='info'>
                    <p class="articleTitle">{{item.articleTitle}}</p>
                    <p class="time">{{item.author}} · {{item.time}}</p>
                    <p class="likeAndComment">

                        <span v-if="item.like" class="icon-in_like_icon"></span>
                        <span v-else class="icon-in_like_border_icon"></span>  
                        <i>12</i>

                        <span class="icon-in_comments_border_icon"></span>
                        <i>{{item.comments}}</i>

                    </p>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
import "../assets/font-icon/style.css";
export default {
  props: ["resData"],
  data() {
    return {};
  },
  methods: {
    enter(id) {
      console.log(id);
    }
  }
};
</script>
<style lang="less" scoped>
@m: 750/375;
// .container {
//   position: relative;
//   width: 100%;
//   box-sizing: border-box;
//   padding: 0 20px * @m;
//   border: 1px solid transparent;
//   font-size: 14px * @m;
//   margin-top: 30px * @m;
// }

.productBox {
  width: 100%;
}

.productBox li {
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.6);
  border-radius: 8px * @m;
  width: 100%;
  height: 313px * @m;
  padding: 10px * @m 10px * @m 0 10px * @m;
  box-sizing: border-box;
  margin-bottom: 20px * @m;
}

.productBox li .pic {
  display: block;
  width: 315px * @m;
  height: 210px * @m;
  border-radius: 8px * @m;
  //   background-image: url("./image/Bitmap111.png");
  //   background-repeat: no-repeat;
  //   background-size: cover;
  /* margin-bottom: 10px; */
}

.productBox li .info {
  padding: 10px * @m 10px * @m 0 10px * @m;
  box-sizing: border-box;
}

.productBox li .info p {
  margin-bottom: 5px * @m;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.productBox li .info .articleTitle {
  font-size: 16px * @m;
  color: #0f2540;
}

.productBox li .info .time {
  font-size: 13px * @m;
  color: #b9b9b9;
  margin-bottom: 8px * @m;
}

.productBox li .info .likeAndComment i {
  display: inline-block;
  margin-right: 20px * @m;
  position: relative;
  bottom: 2px * @m;
  font-size: 12px * @m;
  color: #c9d6de;
  letter-spacing: 0;
}

span {
  font-size: 16px * @m;
  color: #c9d6de;
  margin-right: 5px * @m;
}

.icon-in_like_icon {
  color: #ff8888;
}
</style>



