<template>
    <div style="width: 100%;">
        <div class="container">
            <hotReport :resData='this.rspData'></hotReport>
        </div>
    </div>
</template>

<script>
import hotReport from "../components/hotTestExperienceReport.vue";
export default {
  components: { hotReport },
  data() {
    return {
      rspData: [
        {
          id: 110,
          imgSrc:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          articleTitle: "iPhone X 众测体验报告",
          author: "City",
          time: "1 个月之前",
          like: true,
          comments: 22
        },
        {
          id: 120,
          imgSrc:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          articleTitle: "iPhone X 众测体验报告",
          author: "City",
          time: "1 个月之前",
          like: false,
          comments: 33
        }
      ]
    };
  },
  methods: {
    // 接收data
    getData(data) {
      alert(data);
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
@m: 750/375;
/* 主体区域 */
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
  padding-top: 30px * @m;
}
</style>



