import Vue from 'vue'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

// 承载
import hotTestExperience from '../pages/testExperience.vue'
// 主页
import hotTestExperienceIndex from '../views/test-experience.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'hotTestExperience',
    component: hotTestExperienceIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#hotTestExperience',
  router,
  components: {
    hotTestExperience
  },
  template: '<hotTestExperience/>'
})



